/*
 * LSX Tour Operators
 */

@import 'include-media';
@import 'customizer-to-body-colours';
@import '../vendor/flag-icon-css/flag-icon';

// ---------- Global Variables ----------


$border-grey: #ddd;
$border-grey-dark: #555;

$white: #fff;
$grey-light: #f8f8f8;
$grey: #555;
$black: #333;

$blue: #428bca;
$blue-dark: #2a6496;

$orange: #f7941d;
$orange-dark: #f7741d;


// ---------- Mixins ----------


@mixin box-shadow($args) {
	-webkit-box-shadow: $args;
	-moz-box-shadow: $args;
	-o-box-shadow: $args;
	box-shadow: $args;
}

@mixin text-shadow($args) {
	-webkit-text-shadow: $args;
	-moz-text-shadow: $args;
	-o-text-shadow: $args;
	text-shadow: $args;
}

@mixin border-radius($args) {
	-webkit-border-radius: $args;
	-moz-border-radius: $args;
	-o-border-radius: $args;
	border-radius: $args;
}

// ---------- Generic Placeholders ----------


%clear-list {
	list-style: none;
	padding: 0;
	margin: 0;
}

%clear-div {
	&:after {
		visibility: hidden;
		display: block;
		font-size: 0;
		content: "";
		clear: both;
		height: 0;
	}
}

%clear-panel {
	margin-bottom: 0;
	background: none;
	border: none;
	border-radius: 0;
	box-shadow: none;
}

%currency-icons {
	.currency-icon {
		&:after {
			margin: {
				left: 3px;
				right: 3px;
			}
		}

		&.usd,
		&.nad,
		&.cad,
		&.hkd,
		&.sgd,
		&.nzd,
		&.aud {
			&:after {
				content: '\0024';
			}
		}

		&.eur {
			&:after {
				content: '\20AC';
			}
		}

		&.gbp {
			&:after {
				content: '\00A3';
			}
		}

		&.zar {
			&:after {
				content: '\0052';
			}
		}
		&.brl {
			&:after {
				content: '\0052\0024';
			}
		}
		&.bwp {
			&:after {
				content: '\0050';
			}
		}
		&.cny,
		&.jpy {
			&:after {
				content: '\00A5';
			}
		}
		&.inr {
			&:after {
				content: '\20B9';
			}
		}
		&.idr {
			&:after {
				content: '\20B9';
			}
		}
		&.ils {
			&:after {
				content: '\20AA';
			}
		}
		&.kes {
			&:after {
				content: '\004b';
			}
		}
		&.lak {
			&:after {
				content: '\20ad';
			}
		}
		&.mwk {
			&:after {
				content: '\004d\004b';
			}
		}
		&.myr {
			&:after {
				content: '\0052\004d';
			}
		}
		&.mzn {
			&:after {
				content: '\004d\0054';
			}
		}
		&.nok,
		&.sek {
			&:after {
				content: '\006b\0072';
			}
		}
		&.rub {
			&:after {
				content: '\0440\0443\0431';
			}
		}
		&.chf {
			&:after {
				content: '\0043\0048\0046';
			}
		}
		&.tzs {
			&:after {
				content: '\0054\0053\0068';
			}
		}
		&.aed {
			&:after {
				content: '\062f\002e\0625';
			}
		}
		&.zmw {
			&:after {
				content: '\005a\006b';
			}
		}
		&.zwl {
			&:after {
				content: '\005a\0024';
			}
		}
	}
}

