%lsx-to-navigation {
	margin: -60px 0 50px;

	&.fixto-logged.fixto-fixed {
		padding-top: 32px;
	}

	.container {
		@media only screen and (max-width: 991px) {
			display: none;
		}
	}

	ul {
		@extend %clear-div;
		@extend %clear-list;
		background: $grey-light;
		font-size: 15px;
		margin: 0 -99999px;
		padding: 0 99999px;
		@include box-shadow(0px 1px 1px rgba(0, 0, 0, .15));

		li {
			float: left;
			font-size: 15px;
			line-height: 23px;

			&:last-child {
				margin: 0;
				padding: 0;
			}
		}
	}
}

