// ---------- Panels (Module) Placeholders ----------


%panel-root {
	padding-bottom: 30px;
}

%panel {
	background: $white;
	padding: 20px 20px 25px 20px;
	border: 1px solid $border-grey;
	border-radius: 4px;
	margin-bottom: 0;
	@include box-shadow(1px 1px 8px 0px rgba(0, 0, 0, 0.07));
	@extend %clear-div;

	@media only screen and (max-width: 767px) {
		//max-width: 360px;

		margin: {
			left: auto;
			right: auto;
		}
	}

	.thumbnail {
		margin: {
			right: 0;
			bottom: 16px;
		}

		img {
			width: 100%;
		}
	}

	.title {
		margin: 0;
		font-size: 18px;
		color: $black;

		a {
			color: $black;

			&:hover,
			&:active,
			&:focus {
				color: $blue;
			}
		}
	}

	.tagline {
		margin: 5px 0 0 0;
		font-size: 14px;
		line-height: 20px;
		color: $grey;

		a {
			color: $grey;

			&:hover,
			&:active,
			&:focus {
				color: $blue;
			}
		}
	}

	.content {
		padding: 15px 0 0 0;
		margin: 13px 0 0 0;
		border-top: 1px solid $border-grey;
		font-size: 15px;
		line-height: 23px;

		p {
			margin: 0;
		}
	}

	.widget-content {
		padding-top: 12px;
	}

	.view-more {
		padding-top: 20px;
		margin-top: 12px;
		border-top: 1px solid $border-grey;
		text-align: center;

		&:first-child {
			margin-top: 0;
		}

		.btn {
			white-space: normal;
			line-height: 150%;

			padding: {
				top: 10px;
				bottom: 10px;
			}
		}
	}
}

