%panel-internal {
	@extend %panel;

	// .title
	h3 {
		text-align: left;
		margin: 0;
		font-size: 18px;
		font-weight: 500;
		color: $black;

		a {
			color: $black;

			&:hover,
			&:active,
			&:focus {
				color: $blue;
			}
		}

		// .tagline
		& + strong {
			display: block;
			margin: 5px 0 0 0;
			font-size: 14px;
			line-height: 20px;
			color: $grey;

			a {
				color: $grey;

				&:hover,
				&:active,
				&:focus {
					color: $blue;
				}
			}

			small {
				font-size: 100%;
			}
		}
	}

	.entry-content {
		margin: 10px 0 0 0;
		font-size: 15px;
		line-height: 23px;

		p {
			color: $grey;
			margin: 10px 0 0 0;

			&:first-child {
				margin-top: 0;
			}
		}
	}
}

%panel-internal-inline {
	padding: {
		left: 5px;
		right: 5px;
	}

	[class^="col-"],
	[class*=" col-"] {
		[class^="col-"]:first-child,
		[class*=" col-"]:first-child {
			padding-left: 0;

			@media only screen and (max-width: 767px) {
				padding-right: 0;
			}
		}

		[class^="col-"]:last-child,
		[class*=" col-"]:last-child {
			padding-right: 0;

			@media only screen and (max-width: 767px) {
				padding-left: 0;
			}
		}
	}

	.meta {
		&:first-child {
			@media only screen and (max-width: 767px) {
				margin-top: 12px;
			}

			@media only screen and (min-width: 768px) {
				margin-top: 0;
				padding-top: 0;
				border-top: none;
			}
		}

		&.info {
			@media only screen and (min-width: 768px) {
				.price,
				.duration {
					padding-top: 0;
				}
			}
		}
	}
}


// ---------- Archive (Page) Placeholders ----------


%archive-root {
	&.page-has-banner {
		.wrap {
			margin-top: 0 !important;

			.content {
				padding-top: 60px;
			}
		}
	}

	.wrap {
		margin-bottom: 40px;

		@media only screen and (max-width: 767px) {
			margin-bottom: 20px;
		}

		#primary.col-sm-12,
		#secondary.col-sm-12 {
			max-width: 100% !important;
		}

		#secondary.col-sm-3 {
			margin-top: 0;
		}

		#main {
			& > p:last-of-type {
				margin-bottom: 60px;
			}
		}
	}
}

%archive-description {
	/*padding-top: 60px;*/

	.section-title {
		margin: 0 0 20px;
		font-size: 32px;
		text-align: center;
	}

	p {
		color: $grey;
		font-size: 16px;
		line-height: 25px;
		max-width: 625px !important;
		text-align: center;

		margin: {
			left: auto;
			right: auto;
		}
	}
}

%archive-description-level-2 {
	.section-title {
		margin: 0 0 20px;
		font-size: 24px !important;
		text-align: center;
	}
}

%archive-share-buttons {
	display: none;
}


// ---------- Single (Page) Placeholders ----------


%single-root {
	.wrap {
		margin-bottom: 40px;

		@media only screen and (max-width: 767px) {
			margin-bottom: 20px;
		}

		#primary.col-sm-12 {
			max-width: 100% !important;
		}
	}

	&:not(.top-menu-fixed) {
		.wrap {
			margin-top: 0 !important;
		}
	}

	&.page-has-banner {
		.wrap {
			& > .content {
				padding-top: 0;
			}
		}
	}

	#main {
		padding-top: 0 !important;
	}
}

%single-entry-content {
	.single-main-info {
		@media only screen and (min-width: 768px) {
			float: left;
			width: 100%;
			margin: 0 0 30px 0;
		}

		h3 {
			margin: 0;
			float: left;
		}
	}

	.meta {
		&:first-child {
			padding-top: 0;
			margin-top: 0;
			border-top: none;
		}

		&.info {
			float: right;
			padding-bottom: 0;
			border-bottom: none;

			margin: {
				top: 5px;
				bottom: 0;
			}

			.price,
			.duration {
				font-size: 16px;
				padding: 0;
			}

			.price:not(:last-child),
			.duration:not(:last-child) {
				margin-right: 15px;
			}

			.price:last-child,
			.duration:last-child {
				&:after {
					visibility: hidden;
				}
			}

			@media only screen and (min-width: 768px) {
				.price {
					display: inline-block;
					float: left;
				}

				.duration {
					display: inline-block;
					float: right;
				}
			}
		}

		&.taxonomies {
			position: relative;
			padding: 10px 0 5px 0;
			margin: 45px 0 15px;
			clear: both;
			border-top: 1px solid $border-grey;

			.meta {
				float: left;
				min-height: 29px;

				margin: {
					bottom: 5px;
					right: 25px;
				}

				&:last-child {
					margin-right: 0;

					&:after {
						content: '';
						position: absolute;
						left: 0;
						bottom: 0;
						width: 100%;
						border-bottom: 1px solid $border-grey;
						visibility: visible;
					}
				}
			}
		}
	}

	p,
	ul > li {
		color: $grey;
		font-size: 16px;
		line-height: 25px;
	}

	p {
		.btn.more-link {
			margin-top: 20px;
		}
	}
}

%single-team-member-widget {
	@extend %panel;
	text-align: center;

	.team-member {
		.title {
			font-size: 18px;
		}

		.meta {
			&:first-child {
				margin-top: 17px;
			}
		}
	}

	.enquire-form {
		padding-top: 20px;
		margin-top: 13px;
		border-top: 1px solid $border-grey;
	}
}

%single-hentry {
	margin-bottom: 0;

	& > [class*="col-"] {
		@media only screen and (max-width: 767px) {
			margin-bottom: 15px;
		}
	}

	.team-member-widget {
		@extend %single-team-member-widget;
	}

	.entry-content {
		@extend %single-entry-content;

		@media only screen and (max-width: 767px) {
			h3,
			.meta.info {
			//.meta.info .price,
			//.meta.info .duration {
				float: none !important;
			}

			h3 {
				margin-bottom: 10px !important;
			}

			.meta.taxonomies {
				margin-top: 10px !important;
			}
		}

		@media only screen and (min-width: 768px) {
			.meta.info {
				.price {
					display: inline-block;
					float: left;
					margin-bottom: 0;
				}

				.duration {
					display: inline-block;
					float: right;
				}
			}
		}
	}
}

%single-simple-list {
	margin-top: 50px;

	@media only screen and (max-width: 767px) {
		margin-top: 30px;

		.col-sm-6:last-child {
			margin-top: 30px;
		}
	}

	.section-title {
		color: $black;
		font-size: 18px;
		margin: 0 0 20px;
	}

	.entry-content {
		ul {
			@extend %clear-list;

			li {
				color: $grey;
				font-size: 15px;
				line-height: 23px;

				margin: {
					bottom: 10px;
					left: 20px;
				}

				&:before {
					content: '\f058';
					font-family: fontawesome;

					margin: {
						right: 7px;
						left: -20px;
					}
				}

				ul {
					margin-top: 10px;
				}
			}
		}
	}
}

%single-simple-list-highlights {
	.best-time-to-visit {
		.best-time-to-visit-content {
			@extend %clear-div;

			[class^="col-"] {
				height: 90px;
				overflow: hidden;
				text-align: center;

				border: {
					top: 1px solid $black;
					right: 1px solid $border-grey;
					bottom: 1px solid $black;
				}

				&:nth-of-type(6n-5) {
					border-left: 1px solid $black;
				}

				&:nth-of-type(6n) {
					border-right: 1px solid $black;
				}

				padding: {
					left: 0;
					right: 0;
				}

				small {
					color: $white;
					background-color: $black;
					line-height: 43px;
					display: block;

					font: {
						size: 15px;
						weight: bold;
					}

					padding: {
						left: 15px;
						right: 15px;
					}
				}

				br {
					display: none;
				}

				.fa {
					font-size: 25px;
					color: $grey;
					padding: 10px 0;
				}
			}
		}
	}
}

%single-extra-sections {
	margin-top: 70px;

	@media only screen and (max-width: 767px) {
		margin-top: 30px;
	}

	.section-title {
		color: $black;
		margin: 0 0 40px;
		font-size: 24px;
		text-align: center;
	}
}

%single-extra-sections-terms-and-conditions {
	.entry-content {
		ul {
			@extend %clear-list;

			li {
				color: $grey;
				font-size: 15px;
				line-height: 23px;

				margin: {
					bottom: 10px;
					left: 20px;
				}

				&:before {
					content: '\f058';
					font-family: fontawesome;

					margin: {
						right: 7px;
						left: -20px;
					}
				}

				ul {
					margin-top: 10px;
				}
			}
		}
	}
}

%single-extra-sections-facilities {
	margin-top: 50px;

	@media only screen and (max-width: 767px) {
		margin-top: 30px;
	}

	.row.info {
		@media only screen and (max-width: 767px) {
			& > .col-sm-6:nth-child(n+2) {
				margin-top: 40px;
			}
		}

		@media only screen and (min-width: 768px) {
			& > .col-sm-6:nth-child(n+3) {
				margin-top: 40px;
			}

			& > .col-sm-6:nth-child(2n+3) {
				clear: left;
			}
		}
	}

	h3 {
		color: $black;
		font-size: 18px;
		margin: 0 0 15px;

		padding-bottom: 12px;
		border-bottom: 1px solid $border-grey;

		a {
			color: $black;

			&:hover,
			&:active,
			&:focus {
				color: $blue;
			}
		}
	}

	ul {
		@extend %clear-list;

		li {
			color: $grey;
			font-size: 13px;
			line-height: 15px;
			margin-bottom: 10px;
			padding-left: 0;

			a {
				color: $black;

				&:hover,
				&:active,
				&:focus {
					color: $blue;
				}
			}
		}
	}
}

%single-extra-sections-gallery {
	.envira-gallery-image {
		width: 100%;
	}
}

%single-share-buttons {
	line-height: 1;

	.sharedaddy {
		display: inline-block;
		line-height: 1;

		.sd-sharing {
			display: inline-block;
			clear: both;
			float: left;
			width: 100%;
			padding: 0;

			.sd-title {
				font-size: 13px;
				color: $grey;
				margin: 5px 10px 0 0;
			}

			.sd-content {
				float: left;

				ul {
					li[class*='share-'] {
						a,
						a:hover,
						div.option a {
							padding: 5px;

							&.sd-button:before {
								font-size: 13px;
							}
						}
					}
				}
			}
		}
	}
}


