body.single-tour-operator {
	@extend %single-root;
	// @extend %bootstrap-carousel;

	// ---------- Breadcrumb ----------

	#main > .lsx-to-navigation {
		@extend %lsx-to-navigation;
		margin-top: 0;
	}

	&:not(.page-has-banner) {
		#main > .lsx-to-navigation {
			margin-top: 30px;
		}
	}

	// ---------- Content ----------

	#summary, .lsx-modal {
		article.hentry {
			@extend %single-hentry;
		}
	}

	// ---------- Extra sections ----------

	// #tours,
	// #regions,
	// #accommodation,
	// #destinations,
	// #activities,
	// #activites,
	// #related-items,
	// #itinerary,
	// #chalets,
	// #rooms,
	// #travel-info,
	// #spas,
	// #tents,
	// #villas,
	// #videos,
	// #main > section[id$="-map"],
	// #terms-and-conditions,
	// #gallery,
	// #facilities {
	// 	@extend %single-extra-sections;
	// }

	#terms-and-conditions {
		@extend %single-extra-sections-terms-and-conditions;
	}

	#gallery {
		@extend %single-extra-sections-gallery;
	}

	#facilities {
		@extend %single-extra-sections-facilities;
	}

	// ---------- Extra sections (without big title) ----------

	#highlights {
		@extend %single-simple-list;
		@extend %single-simple-list-highlights;
	}

	#included-excluded {
		@extend %single-simple-list;
	}

	#features {
		@extend %single-simple-list;
	}

	// ---------- Extra sections (panels style) ----------

	#tours,
	#regions,
	#accommodation,
	#destinations,
	#activities,
	#activites,
	#related-items,
	#videos {
		.panel {
			@extend %clear-panel;
			@extend %panel-root;

			& > article {
				@extend %panel-internal;
			}
		}

		.col-sm-12 {
			& > article.hentry {
				@extend %panel-internal-inline;
			}
		}
	}

	#itinerary {
		.panel {
			@extend %clear-panel;
			@extend %panel-root;

			//& > .itinerary-inner {
			.itinerary-inner {
				@extend %panel-internal;
				@extend %panel-internal-inline;
			}
		}
	}

	#rooms,#chalets,#tents,#spas,#villas,
	#travel-info {
		.panel {
			& > .unit {
				@extend %panel-internal;
				@extend %panel-internal-inline;

				//@media only screen and (min-width: 768px) {
				//	.unit-info {
				//		padding: 15px 0;
				//	}
				//}
			}

			@media only screen and (min-width: 768px) {
				&.col-sm-6:nth-child(2n+1) {
					clear: left;
				}
			}
		}
	}

	#regions,
	#destinations,
	#itinerary {
		.meta:first-child {
			@media only screen and (min-width: 768px) {
				padding-top: 0 !important;
				margin-top: 0 !important;
				border-top: none !important;
			}
		}
	}

	#videos {
		.video {
			h3,
			.entry-content {
				margin-top: 15px !important;
			}
		}
	}

	// ---------- Share buttons ----------

	#sharing {
		@extend %single-share-buttons;
	}

	.modal-body {
		article {
			.entry-content {
				.moretag {
					width: 230px;
					display: block;
					margin-left: auto !important;
					margin-right: auto !important;
					padding-left: 30px !important;
					padding-right: 30px !important;
				}
			}
		}
	}
}


