.lsx-to-widget-itens { margin-top: -4.25rem; }

.lsx-to-widget-item-wrap {
	margin-top: 4.25rem;

	.lsx-to-slider-inner & { padding: 0 15px; }
}

.lsx-to-slider-inner {
	@include media('>=tablet') {
		margin: 0 auto;
		width: 100%;

		&.slick-has-arrows { width: calc(100% - 10.8rem); }
	}

	.slick-dots {
		align-items: center;
		display: flex;
		flex-flow: row wrap;
		height: auto;
		justify-content: center;
		list-style: none;
		margin: 2rem 0 0;
		padding: 0;

		& > li {
			line-height: 1;
			padding: 5px;

			@include media('>=tablet') { padding: 0 7.5px; }

			& > button {
				background: transparent;
				background-clip: padding-box;
				border-style: solid;
				border-width: 1px;
				font: 0/0 a;
				height: 6px;
				transition: all 300ms ease-in-out;
				width: 40px;
			}
		}
	}

	.slick-arrow {
		background: transparent;
		border-radius: 50%;
		border-style: solid;
		border-width: 2px;
		font: 0/0 a;
		height: 5.4rem;
		margin-top: calc((2.25rem - 17px) / 2);
		opacity: 1;
		padding: 0;
		position: absolute;
		text-shadow: none;
		top: 50%;
		transform: translateY(-50%);
		transition: border 300ms ease;
		width: 5.4rem;
		z-index: 3;

		&:before {
			display: block;
			font-family: 'FontAwesome';
			font-size: 4rem;
			line-height: 1;
			position: absolute;
			text-rendering: auto;
			top: 50%;
			transform: translateY(-50%);
			transition: color 300ms ease;

			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}

		&.slick-disabled { cursor: not-allowed; }

		&.slick-prev {
			left: -5.4rem;

			&:before {
				content: '\f104';
				left: 1.5rem;
			}
		}

		&.slick-next {
			right: -5.4rem;

			&:before {
				content: '\f105';
				left: 2rem;
			}
		}
	}
}

.lsx-to-widget-view-all {
	margin-top: 6rem;
	overflow: hidden;
	position: relative;
	text-align: center;

	& > span {
		display: inline-block;
		position: relative;

		&:before,
		&:after {
			content: '';
			display: block;
			height: 2px;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: 999rem;
		}

		&:before { right: 120%; }
		&:after { left: 120%; }
	}
}

.lsx-to-widget-item-wrap {
	.lsx-to-widget-content {
		background-color: #FFF;
		padding: 2.4rem 2rem 0;

		.moretag {
			display: block;
			margin: 0 -2rem;
			padding: 1rem 0;
			text-align: center;
			transition: background 300ms ease;

			&:after {
				color: inherit;
				content: '\f178';
				display: inline-block;
				font-family: 'FontAwesome';
				font-size: inherit;
				margin-left: 5px;
				text-rendering: auto;

				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}

		p:last-child { margin-bottom: 0; }
	}

	.lsx-to-widget-tagline { margin-top: -1.2rem; }

	&.lsx-team {
		.lsx-to-widget-thumb {
			border-radius: 50%;
			height: 170px;
			margin: 0 auto 12px;
			overflow: hidden;
			position: relative;
			width: 170px;
		}
	}

	&.lsx-review {
		.lsx-to-widget-thumb {
			border-radius: 50%;
			height: 100px;
			margin: 0 auto 12px;
			overflow: hidden;
			position: relative;
			width: 100px;
		}

		.lsx-to-widget-blockquote {
			@include media('>=tablet') { max-width: none; }
		}
	}

	.lsx-to-widget-thumb img {
		width: 100%;

		&.lazyload, &.lazyloading { width: auto; }
	}
}

.lsx-to-widget-meta-data {
	margin: 2.4rem 0;
	position: relative;
}

.lsx-to-meta-data {
	display: block;
	font-size: 13px;
	padding-left: 22px;
	position: relative;

	.lsx-to-contact-meta-data & { padding-left: 0; }

	& + .lsx-to-meta-data { margin-top: 5px; }

	&:before {
		color: inherit;
		display: inline-block;
		font-family: 'FontAwesome';
		font-size: inherit;
		left: 0;
		position: absolute;
		text-rendering: auto;

		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	&.lsx-to-meta-data-accommodations:before { content: '\f015'; }
	&.lsx-to-meta-data-activities:before { content: '\f1e5'; }
	&.lsx-to-meta-data-brand:before { content: '\f1ad'; }
	&.lsx-to-meta-data-destinations:before { content: '\f0ac'; }
	&.lsx-to-meta-data-duration:before { content: '\f133'; }
	&.lsx-to-meta-data-email:before { content: '\f0e0'; }
	&.lsx-to-meta-data-friendly:before { content: '\f046'; }
	&.lsx-to-meta-data-languages:before { content: '\f1ab'; }
	&.lsx-to-meta-data-phone:before { content: '\f095'; }
	&.lsx-to-meta-data-price:before { content: '\f0d6'; }
	&.lsx-to-meta-data-rating:before { content: '\f006'; }
	&.lsx-to-meta-data-role:before { content: '\f0c0'; }
	&.lsx-to-meta-data-rooms:before { content: '\f236'; }
	&.lsx-to-meta-data-skype:before { content: '\f17e'; }
	&.lsx-to-meta-data-special:before { content: '\f069'; }
	&.lsx-to-meta-data-style:before { content: '\f02b'; }
	&.lsx-to-meta-data-team:before { content: '\f007'; }
	&.lsx-to-meta-data-tours:before { content: '\f072'; }
	&.lsx-to-meta-data-travel-dates:before { content: '\f073'; }
	&.lsx-to-meta-data-type:before { content: '\f292'; }
	&.lsx-to-meta-data-valid-from:before { content: '\f073'; }
}

.archive.archive-tour-operator,
.search.search-results.archive-tour-operator {
	.site-main { width: 100%; }
}

.lsx-to-archive-description {
	& > p {
		&:last-child,
		&:only-child {
			margin-bottom: 0;
		}
	}
}

.lsx-to-archive-items {
	align-items: flex-start;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
	margin-top: -4.25rem;
	width: calc(100% + 30px);

	&:after,
	&:before {
		display: none;
	}
}

.lsx-to-archive-items-separator.lsx-title {
	margin-top: 4.25rem;

	&:first-child {
		margin-top: 0;
	}
}

.lsx-to-archive-item {
	margin-top: 4.25rem;

	& > article[id^='post-'] {
		align-items: stretch;
		background-color: #FFF;
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-start;

		.lsx-to-archive-template-list & {
			@include media('>=phone') { flex-wrap: nowrap; }
		}

		&.destination {
			.lsx-to-archive-template-grid & {
				.entry-content {
					& > p { margin-bottom: 0; }
				}
			}
		}

		&.team {
			.lsx-to-meta-data-socials {
				padding-left: 0;

				& > a {
					font-size: 17px;

					& + a { margin-left: 5px; }
				}
			}

			.lsx-to-archive-template-grid & {
				background-color: transparent;

				.lsx-to-archive-content-title { margin-top: 2.4rem; }

				.lsx-to-archive-wrapper { padding: 0; }

				.lsx-to-archive-thumb {
					border-radius: 50%;
					height: 170px;
					margin: 0 auto;
					overflow: hidden;
					position: relative;
					width: 170px;
				}

				.entry-content { text-align: center; }

				.lsx-to-archive-meta-data {
					text-align: center;

					.lsx-to-meta-data {
						padding-left: 0;

						&:before {
							left: auto;
							margin-right: 5px;
							position: relative;
						}
					}
				}
			}

			.lsx-to-archive-template-list & {
				.lsx-to-archive-thumb {
					& > a {
						display: block;
						height: 0;
						overflow: hidden;
						padding: 0 0 100%;
						position: relative;

						& > img {
							bottom: 0;
							left: 0;
							position: absolute;
							height: 100%;
							right: 0;
							top: 0;
							width: 100%;
						}
					}
				}
			}
		}

		& > .moretag {
			display: block;
			padding: 1rem 0;
			text-align: center;
			transition: background 300ms ease;
			width: 100%;

			&:after {
				color: inherit;
				content: '\f178';
				display: inline-block;
				font-family: 'FontAwesome';
				font-size: inherit;
				margin-left: 5px;
				text-rendering: auto;

				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}
	}

	.lsx-to-single-link {
		margin-bottom: 0;
		margin-top: 2.4rem;
	}
}

.lsx-to-archive-thumb {
	width: 100%;

	@include media('<phone') {
		& > a {
			background-image: none !important;

			& > img { width: 100%; }
		}
	}

	.lsx-to-archive-template-list & {
		@include media('>=phone') {
			flex-shrink: 0;
			position: relative;
			width: 25%;

			& > a {
				background-position: center center;
				background-repeat: no-repeat;
				background-size: cover;
				height: 100%;
				left: 0;
				position: absolute;
				top: 0;
				width: 100%;

				& > img { display: none; }
			}
		}
	}

	.lsx-to-archive-template-grid & {
		& > a {
			background-image: none !important;
			display: block;

			& > img { width: 100%; }
		}
	}
}

.lsx-to-archive-wrapper {
	padding: 20px;

	.lsx-to-archive-template-list & {
		@include media('>=phone') {
			align-items: flex-start;
			display: flex;
			flex-flow: row nowrap;
			flex-grow: 1;
			justify-content: flex-end;
			min-height: 130px;
			padding: 2.4rem 15px;
		}

		@include media('>=tablet') { min-height: 170px; }
		@include media('>=desktop') { min-height: 200px; }
	}

	.lsx-to-archive-template-grid & { width: 100%; }
}

.lsx-to-archive-content {
	.lsx-to-archive-template-list & {
		@include media('>=phone') {
			flex-grow: 1;
			padding-right: 15px;
		}
	}

	.entry-content {
		p:last-child { margin-bottom: 0; }

		.moretag {
			.lsx-to-archive-template-grid & { display: none; }

			&:after {
				color: inherit;
				content: '\f178';
				display: inline-block;
				font-family: 'FontAwesome';
				font-size: inherit;
				margin-left: 5px;
				text-rendering: auto;

				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}
	}
}

.lsx-to-archive-meta-data {
	margin-top: 2.4rem;

	.lsx-to-archive-template-list & {
		@include media('>=phone') {
			flex-shrink: 0;
			margin-top: 0;
			padding-left: 15px;
			width: 40%;
		}

		@include media('>=tablet') { width: 33%; }
	}
}

.lsx-to-archive-content-title {
	.lsx-to-archive-template-grid & {
		text-align: center;

		&:only-child { margin-bottom: 0; }
	}
}

.lsx-to-archive-content-tagline {
	&:last-child { margin-bottom: 0; }

	@include media('>=phone') { margin-top: -1.4rem; }

	.lsx-to-archive-template-grid & { text-align: center; }
}

.lsx-to-contact-thumb {
	border-radius: 50%;
	height: 170px;
	margin: 2.4rem auto 0;
	overflow: hidden;
	position: relative;
	width: 170px;

	@include media('>=phone') {
		margin-top: 0;

		& + .lsx-to-contact-prefix { margin-top: 2.4rem; }
	}
}

.lsx-to-contact-prefix {
	display: block;
	font-size: 13px;
}

.lsx-to-contact-socials {
	& > a {
		font-size: 17px;
		line-height: 1;
		padding: 0 5px;
	}
}

